<template>
    <div>
        <v-snackbar
                v-model="snackbar"
                color="success"
                :right="true"
                :top="true"
        >
               
            <v-btn
                    text
                    color="success"
                    @click="snackbar = false"
            >
                Close
            </v-btn>
        </v-snackbar>
        <v-expansion-panels v-model="isOpen" multiple accordion light>

            <v-expansion-panel v-model="isOpen">
                <v-expansion-panel-header color="#0070C0"><span :style="{color: 'white', textAlign: 'center'}"> </span>
                </v-expansion-panel-header>
                <v-expansion-panel-content eager="true">
                    <v-app>
                        <v-text-field v-model="valueFields.email" label="E-mail *"></v-text-field>
                        <v-text-field v-model="password" label=" *"></v-text-field>
                        <v-text-field v-model="valueFields.nameCompany"
                                      label=" ,  *"></v-text-field>
                        <v-text-field v-model="valueFields.legalAddress" label="  *"></v-text-field>
                        <v-text-field v-model="valueFields.postAddress" label="  *"></v-text-field>
                        <v-text-field v-model="valueFields.postcode" label=" *"></v-text-field>
                        <v-text-field v-model="valueFields.unp" label="( ) *"></v-text-field>
                        <v-menu
                                v-model="menu2"
                                :close-on-content-click="false"
                                :nudge-right="40"
                                transition="scale-transition"
                                offset-y
                                min-width="100px"
                        >
                            <template v-slot:activator="{ on }">
                                <v-text-field
                                        v-model="date"
                                        label=" "
                                        readonly
                                        v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker v-model="date" @input="menu2 = false"></v-date-picker>
                        </v-menu>
                        <v-text-field v-model="valueFields.whoIssued" label=" "></v-text-field>
                        <v-text-field v-model="valueFields.paymentAccount" label=" "></v-text-field>
                        <v-text-field v-model="valueFields.nameBank" label=" "></v-text-field>
                        <v-text-field v-model="valueFields.codeBank" label=" "></v-text-field>
                        <v-text-field v-model="valueFields.addressBank" label=" "></v-text-field>
                        <v-text-field v-model="valueFields.fullName" label="...   *"></v-text-field>
                        <v-text-field v-model="valueFields.phoneNumber"
                                      label=" (: (029) 1111111) *"></v-text-field>
                    </v-app>
                </v-expansion-panel-content>
            </v-expansion-panel>

            <v-expansion-panel>
                <v-expansion-panel-header color="#0070C0"><span :style="{color: 'white',textAlign : 'center'}"> </span>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                    <v-col class="d-flex flex-column" cols="12" sm="6">
                        <v-select
                                :items="countrys"
                                v-model="address.country"
                                filled
                                label=""
                        ></v-select>
                        <v-text-field
                                v-model="address.region"
                                label="/"
                                required
                        ></v-text-field>
                        <v-text-field
                                v-model="address.city"
                                label=""
                                required
                        ></v-text-field>
                        <v-text-field
                                v-model="address.street"
                                label=""
                                required
                        ></v-text-field>
                        <v-text-field
                                v-model="address.home"
                                label=""
                                required
                        ></v-text-field>
                        <v-text-field
                                v-model="address.body"
                                label=""
                                required
                        ></v-text-field>
                        <v-text-field
                                v-model="address.apartment"
                                label=", , :"
                                required
                        ></v-text-field>
                        <v-textarea
                                outlined
                                name="input-7-4"
                                label=""
                                :v-model="address.comment"
                        ></v-textarea>
                    </v-col>
                </v-expansion-panel-content>
            </v-expansion-panel>
        </v-expansion-panels>
        <div :style="{display: 'flex', justifyContent: 'space-between'}">
            <div class="button" @click="updateUser" :style="{cursor: 'pointer'}">
                
            </div>

            <div class="button" @click="()=>{}">
                
            </div>

            <div class="button" @click="()=>{}">
                 
            </div>
        </div>
    </div>
</template>

<script>
    import UserService from "../../services/user";
    export default {
        name: 'Registration',
        data: () => ({
            date: new Date().toISOString().substr(0, 10),
            email: '',
            iAgree: false,
            password: '',
            menu: false,
            modal: false,
            snackbar: false,
            menu2: false,
            isOpen: [0],
            valueFields: {
                nameCompany: '',
                legalAddress: '',
                postAddress: '',
                postcode: '',
                email: '',
                unp: '',
                whoIssued: '',
                paymentAccount: '',
                nameBank: '',
                codeBank: '',
                addressBank: '',
                fullName: '',
                phoneNumber: ''
            },
            countrys: [
                "",
                "",
                "",
                "",
                "",
                "",
                "  ",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                " - ",
                "  ",
                "",
                "",
                "",
                " ",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            address: {
                country: '',
                region: '',
                city: '',
                street: '',
                home: '',
                body: '',
                apartment: '',
                comment: ''
            }
        }),
        methods: {
            init() {
                const dataUser = this.$store.state.User.dataUser;
                this.valueFields.nameCompany = dataUser.nameCompany;
                this.valueFields.legalAddress = dataUser.legalAddress;
                this.valueFields.postAddress = dataUser.postAddress;
                this.valueFields.postcode = dataUser.postcode;
                this.valueFields.email = dataUser.email;
                this.valueFields.unp = dataUser.unp;
                this.valueFields.whoIssued = dataUser.whoIssued;
                this.valueFields.paymentAccount = dataUser.paymentAccount;
                this.valueFields.nameBank = dataUser.nameBank;
                this.valueFields.codeBank = dataUser.codeBank;
                this.valueFields.addressBank = dataUser.addressBank;
                this.valueFields.fullName = dataUser.fullName;
                this.valueFields.phoneNumber = dataUser.phoneNumber;

                this.address.country = dataUser.country;
                this.address.region = dataUser.region;
                this.address.city = dataUser.city;
                this.address.street = dataUser.street;
                this.address.home = dataUser.home;
                this.address.body = dataUser.body;
                this.address.apartment = dataUser.apartment;
                this.address.comment = dataUser.comment;
            },
            updateUser(){
                UserService.updateUser(localStorage.getItem('token'), {...this.valueFields, ... this.address});
                this.$store.commit('setDataUser', {...this.valueFields, ...this.address});
                this.snackbar = true;
                setTimeout(()=>{
                    this.snackbar = false;
                    setTimeout(()=>{
                        this.$router.go(-1);
                    }, 1000)
                }, 1500)
            }
        },
        created() {
            setTimeout(()=>{
                this.init();
            }, 1500)
        }
    }
</script>

<style scoped>
    .button {
        padding: 13px;
        margin: 20px;
        width: 250px;
        height: 50px;
        background-color: #00B0F0;
        color: white;
        text-align: center;
    }

    .button:hover {
        background-color: #006C9C;
    }
</style>