<template>
    <div class="confirmation-order">
                      
          .<br><br>
                ,       
         ,     .<br><br>

        <v-checkbox
                v-model="checkbox"
                value="1"
                label="  (-)     "
                type="checkbox"
                :error="error.checkbox"
                :style="{width: 'fit-content'}"
                required
        ></v-checkbox>
        <Button :text-button="' '" :width="'210px'" @click="sendOrder"/>
    </div>
</template>

<script>
    import Button from "../../shared/elements/Button";

    export default {
        name: 'ConfirmationOrder',
        components: {
            Button
        },
        data: () => ({
            checkbox: false,
            error: {
                checkbox: false
            }
        }),
        methods: {
            sendOrder() {
                if (this.checkbox) {
                    this.error.checkbox = false;
                    this.$emit('emitSendOrder')
                } else {
                    this.error.checkbox = true;
                }
            }
        }
    }
</script>

<style scoped>
    .confirmation-order {
        padding: 30px;
    }
</style>