<template>
    <div class="about">
        {{text ? text : defaultText}}
    </div>
</template>

<script>
    import AboutService from "../../services/about";

    export default {
        name: 'About',
        data: () => ({
            text: '',
            defaultText: '            \n         .      ,   \n            , , .\n             ,  ,  ,  \n               .\n                     ,\n            .         , ,\n        ,           .  \n              :     , \n         ,    ,  ,   \n        ,  ,     .\n                     . \n            , , , ,     .     \n             ,     ,  \n         ,    , .      , \n              .     ,    \n        ,   ,    .\n                   \n             .     , \n             .      - . \n                   . \n              ,  ,      \n        .'
        }),
        created() {
            AboutService.getAbout().then(({data}) => {
                this.text = data[0].about;
            })
        }
    }

</script>

<style>
    .about {
        padding: 10px;
        font-weight: bold;
    }
</style>