<template>
    <div class="address-shipping">
        <div>
            <v-col class="d-flex flex-column" cols="12" sm="6">
                <v-select
                        :items="countrys"
                        v-model="address.country"
                        filled
                        label=""
                ></v-select>
                <v-text-field
                        v-model="address.region"
                        label="/"
                        required
                ></v-text-field>
                <v-text-field
                        v-model="address.city"
                        label=""
                        required
                ></v-text-field>
                <v-text-field
                        v-model="address.street"
                        label=""
                        required
                ></v-text-field>
                <v-text-field
                        v-model="address.home"
                        label=""
                        required
                ></v-text-field>
                <v-text-field
                        v-model="address.body"
                        label=""
                        required
                ></v-text-field>
                <v-text-field
                        v-model="address.apartment"
                        label=", , :"
                        required
                ></v-text-field>
                <v-textarea
                        outlined
                        name="input-7-4"
                        label=""
                        :v-model="address.comment"
                ></v-textarea>
            </v-col>
            <Button :text-button="''" @click="nextStep"/>
        </div>
    </div>
</template>

<script>
    import Button from "../../shared/elements/Button";

    export default {
        name: 'AddressShipping',
        components: {
            Button
        },
        computed: {
          dataUser(){
              return this.$store.state.User.login;
          }
        },
        watch: {
          dataUser(){
              console.log('dataUser')
              this.initFields();
          }
        },
        data: () => ({
            countrys: [
                "",
                "",
                "",
                "",
                "",
                "",
                "  ",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                " - ",
                "  ",
                "",
                "",
                "",
                " ",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            address: {
                country: '',
                region: '',
                city: '',
                street: '',
                home: '',
                body: '',
                apartment: '',
                comment: ''
            }
        }),
        methods: {
            nextStep() {
                this.$emit('emitAddAddressShipping', this.address)
            },
            async initFields() {
                const dataUser = await this.$store.state.User.dataUser;
                this.address.country = dataUser.country;
                this.address.region = dataUser.region;
                this.address.city = dataUser.city;
                this.address.street = dataUser.street;
                this.address.home = dataUser.home;
                this.address.body = dataUser.body;
                this.address.apartment = dataUser.apartment;
                this.address.comment = dataUser.comment
            }
        },
        created() {
            this.initFields();
        }
    }
</script>

